import {mapState,mapMutations} from 'vuex'
export default {
	data() {
		return {
			
		}
	},
	computed:{
		...mapState(['personInfo'])
	},
	methods: {
		...mapMutations(['login']),
		...mapMutations(['setPersonInfo']),
		logout: function() {
			uni.removeStorageSync('token')
			this.login(null)
			this.setPersonInfo(null)
			uni.reLaunch({
				url: '../index/index'
			})
		}
	}
}
